<template>
  <div class="home">
    <div class="hello" v-if="allHistory.length === 0">
      <h1>Список проверок пуст, запустите новую проверку</h1>
      <my-button @click="showDialogAll" class="hello__btn">Запустить</my-button>
    </div>
    <div class="hist__list" v-else>
      <h1>Список проведенных проверок</h1>
      <history-list :history="allHistory" />
      <div class="btn__container">
        <my-button @click="openTab" style="width: 150px; margin-right: 10px"
          >Открыть в JSON</my-button
        >
        <my-button
          @click="showDialogOne"
          style="width: 120px; margin-right: 10px"
          >Проверить</my-button
        >
        <my-button @click="showDialogAll">Запустить</my-button>
      </div>
    </div>
    <my-dialog v-model:show="dialogVisibleAll">
      <plan-form @create="addHistory" />
    </my-dialog>
    <my-dialog v-model:show="dialogVisibleOne" @destroy="clearSelection">
      <div class="recviz">
        <div class="recvz__head">
          <h3>Заполните реквизиты</h3>
          <my-button @click="showDialogOne">Закрыть</my-button>
        </div>

        <table-input :columns="$store.state.columns" />
        <h3 v-show="responseEmpty">Совпадений не найдено</h3>
        <my-button class="recviz__btn" @click="sendReqvs">Отправить</my-button>
      </div>
    </my-dialog>
  </div>
</template>

<script>
// @ is an alias to /src
import HistoryList from "@/components/HistoryList.vue";
import PlanForm from "../components/LoadIndicator.vue";
import MyButton from "../components/UI/MyButton.vue";
import { mapState, mapGetters } from "vuex";
import MyInput from "../components/UI/MyInput.vue";
import TableInput from "../components/TableInput.vue";
export default {
  name: "Home",
  components: {
    HistoryList,
    PlanForm,
    MyButton,
    MyInput,
    TableInput,
  },
  data() {
    return {
      inputRecvs: "",
      dialogVisibleAll: false,
      dialogVisibleOne: false,
      responseEmpty: false,
    };
  },
  methods: {
    clearSelection() {
      console.log("exited form");
      this.$store.commit("clearColumnsFields");
      this.$store.commit("clearColumnsSelection");
    },
    showDialogAll() {
      this.dialogVisibleAll = true;
    },
    showDialogOne() {
      this.dialogVisibleOne = !this.dialogVisibleOne;
    },
    addHistory(history) {
      this.history.push(history);
    },
    sendReqvs() {
      let cols = this.$store.state.columns;
      console.log(cols);
      let body = [];
      for (let index = 0; index < cols.length; index++) {
        const element = {
          id: index,
          field: cols[index].field,
        };
        body.push(element);
      }
      console.log(JSON.stringify(body));
      this.$store.commit("clearColumnsSelection");

      const options = {
        method: "POST",
        body: JSON.stringify(body),
      };
      fetch(`http://localhost:${this.$store.state.port}/search-one`, options)
        .then((response) => response.json())
        .then((response) => {
          console.log(response);
          if (response.ids.length != 0) {
            this.responseEmpty = false;
            for (let index = 0; index < cols.length; index++) {
              for (let j = 0; j < response.ids.length; j++) {
                const element = response.ids[j];
                if (index === element) {
                  this.$store.state.columns[index].selected = true;
                }
              }
            }
          } else {
            this.responseEmpty = true;
          }
        });
    },
    openTab() {
      let data = JSON.stringify(this.allHistory);
      var tab = window.open("about:blank", "_blank");
      tab.document.write(data);
      tab.document.close();
    },
  },
  computed: {
    ...mapState({
      history: (state) => state.history,
    }),
    ...mapGetters({
      allHistory: "getHistory",
    }),
  },
  mounted() {
    this.$store.commit("loadHistory");
    // this.history = this.$store.getters.getHistory;
  },
};
</script>

