<template>
  <my-button @click="openHome" class="back__btn">Назад</my-button>
  <div class="container">
    <h1>Информация о проведенной проверке</h1>

    <h3>Перечень: {{ item.pName }}</h3>
    <h3>Дата проверки: {{ item.date }}</h3>

    <div class="table" v-if="item.rows.length != 0">
      <div class="hrs">
        <my-button @click="openTab" style="width: 200px"
          >Открыть в JSON</my-button
        >
        <div
          class="icons8-info"
          title="Зелёным цветом отмечены ячейки, которые совпали с данными из Перечня"
        ></div>
      </div>

      <my-table
        :rows="item.rows"
        :columns="item.columns"
        style="margin-top: 10px"
      />
    </div>

    <h3 v-else><u>Совпадений не найдено</u></h3>
  </div>
</template>

<script>
import MyTable from "@/components/Table.vue";
export default {
  components: { MyTable },

  data() {
    return {
      item: {
        date: "2022.04.13 13:07",
        pName: "ter.xml",
        columns: ["ID", "fio", "passport", "inn_ogrn", "address"],
        rows: [
          [
            { field: "10673", selected: false },
            { field: "Abdul Rehman", selected: true },
            { field: "10", selected: false },
            { field: "10", selected: false },
            { field: "Charterhouse  Street, 5483", selected: false },
          ],
        ],
      },
    };
  },
  methods: {
    openTab() {
      let data = JSON.stringify(this.item);
      var tab = window.open("about:blank", "_blank");
      tab.document.write(data);
      tab.document.close();
    },
    openHome() {
      this.$router.push(`/`);
    },
  },
  mounted() {
    let history = this.$store.getters.getHistory;
    let ind = history.findIndex((i) => i.id === this.$route.params.id);
    this.item = history[ind];
    console.log(this.item);
  },
};
</script>

<style>
.container {
  /* margin: auto; */
  margin-left: 70px;
  display: flex;
  flex-direction: column;
}
.back__btn {
  position: fixed;
  left: 0px;
  padding: auto;
  margin-left: 5px;
  margin-top: 5px;
}
.table {
  margin-top: 20px;
}
.border_bottom_red {
  border-bottom: 1px solid #f00;
}
.icons8-info {
  display: inline-block;
  margin-left: 10px;
  width: 40px;
  height: 40px;
  background: url("data:image/svg+xml;base64,")
    50% 50% no-repeat;
  background-size: 100%;
}
.hrs {
  display: flex;
  flex-direction: row;
}
</style>